body {
  margin: 0;
  font-family: SF Pro Text, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html,
body,
#root {
  height: 100%;
  overflow: hidden;
  font-size: 0.14rem;
}
li {
  list-style: none;
}
ul,
p {
  margin: 0;
  padding: 0;
}
.icon-default {
  display: inline-block;
  background-size: 100% 100%;
  width: 0.3rem;
  height: 0.3rem;
}
.icon_home {
  background-image: url("./images/icon_tab_Home_selected@2x.png");
}
.icon_orders {
  background-image: url("./images/icon_tab_order_def@2x.png");
}
.icon_me {
  background-image: url("./images/icon_tab_me_def@2x.png");
}
#scratch {
  position: relative;
}
#cover {
  position: absolute;
  top: 0;
  left: 0;
}
#card {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 100%;
  width: 100%;
}
