.evaluate_view {
  height: 100%;
  overflow: auto;
  background: #f6f5f4;
  -webkit-overflow-scrolling: touch;
}
.view {
  background: #f6f5f4;
}
.view_body {
  padding: 0.2rem 0.24rem;
  border-radius: 24px 24px 0px 0px;
  background: #fff;
  box-sizing: border-box;
}
.section,
.dishes_section {
  border-bottom: 1px solid rgba(39, 35, 31, 0.1);
  padding-bottom: 0.24rem;
}
.title {
  font-size: 0.12rem;
  color: rgba(210, 209, 204, 1);
}
.big_star {
  text-align: center;
}
.big_star img {
  width: 0.32rem;
  height: 0.32rem;
}
.res_evaluate_section {
  margin-bottom: 0.16rem;
}
.res_evaluate_section {
  display: flex;
  align-items: center;
}
.res_evaluate_section > img {
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.1rem;
  border-radius: 0.08rem;
}
.res_evaluate_section span {
  flex: 1;
  font-size: 16px;
  font-weight: bold;
  color: rgba(39, 35, 31, 1);
}
.res_arrow {
  border: 1px solid rgba(232, 229, 227, 1);
  border-radius: 16px;
  padding: 0 0.08rem;
}
.hide {
  display: none;
}
.detail_more {
  background-image: url("../../images/icon_nav_arrow_up@2x.png");
  background-size: 100% 100%;
  display: inline-block;
  width: 0.16rem;
  height: 0.16rem;
  position: relative;
  top: 0.01rem;
}
.show_more .detail_more {
  background-image: url("../../images/icon_nav_arrow_down.png");
}
.evaluate_info ul li {
  height: 0.62rem;
  width: 0.62rem;
}
.evaluate_info :global(.am-image-picker-list) {
  margin-bottom: 0px;
}
.evaluate_info :global(.am-image-picker-list .am-image-picker-upload-btn) {
  background: url("../../images/icon_rate_add_photo@2x.png") no-repeat;
  background-size: 100% 100%;
  border: none;
}
.evaluate_info
  :global(.am-image-picker-list .am-image-picker-upload-btn::after) {
  display: none;
}
.evaluate_info
  :global(.am-image-picker-list .am-image-picker-upload-btn::before) {
  display: none;
}
.photo_upload {
  background: url("../../images/icon_rate_add_photo@2x.png");
  height: 0.62rem;
  width: 0.62rem;
  display: inline-block;
}
.res_evaluate {
  margin-top: 0.24rem;
}
.evaluate_info {
  background: rgba(246, 245, 244, 1);
  border-radius: 16px;
  padding: 0.16rem;
  box-sizing: border-box;
  margin-top: 0.16rem;
}
.evaluate_info p {
  line-height: 24px;
}
.evaluate_info ul {
  margin-top: 0.24rem;
}
.evaluate_info ul li {
  width: 0.62rem;
  height: 0.62rem;
}
.evaluate_info ul li img {
  width: 100%;
  height: 100%;
}
.dishes_section .title {
  margin-top: 0.24rem;
}
.dishes_section .big_star {
  text-align: left;
}
.dishes_section .big_star img {
  width: 0.24rem;
  height: 0.24rem;
  margin-top: 0.05rem;
}
.dishes_section .res_evaluate_section > img {
  width: 0.66rem;
  height: 0.66rem;
}
.dishes_section .res_evaluate_section span {
  font-size: 0.14rem;
}

.confirm_footer {
  margin-top: 0.06rem;
  margin-bottom: 0.46rem;
}
.confirm_footer span {
  display: block;
  border-radius: 0.22rem;
  font-size: 0.16rem;
  font-weight: bold;
  color: rgba(39, 35, 31, 1);
  padding: 0.18rem 0;
  margin: 0 0.32rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffc765;
}
.review_form {
  border: none;
  background: none;
  outline: none;
  width: 100%;
  display: inline-block;
  height: 60px;
  resize: none;
}
:global(.am-image-picker-item-remove) {
  display: none;
}
